import React from 'react';
import Bottom from './Bottom';
import './store.css';
const ruler=(props)=>{
    return(
        <div>
            <div>
                <i style={{fontSize:50,display:"block"}} 
                className='iconfont icon-fanhui'
                onClick={() =>props.history.go(-1)}></i>
                <img src='https://gw.alicdn.com/bao/uploaded/i3/858389760/O1CN01U30haH2Ly8D8UY5eL_!!858389760-0-lubanu-s.jpg_500x500q90.jpg_.webp' 
                style={{height:300,width:300,margin:40}}/>
            </div>
            <div>
                <Bottom/>
                <button className='button1'>加入购物车</button>
                <button className='button2'>立即购买</button>
            </div>
        </div>
    )
}
export default ruler;