import React from 'react';
import Bottom from './Bottom';
import './store.css';
const strawberry=(props)=>{
    return(
        <div>
            <i style={{fontSize:50,display:"block"}} 
            className='iconfont icon-fanhui'
            onClick={() =>props.history.go(-1)}></i>
            <img src='https://gw.alicdn.com/bao/uploaded/i1/1030481247/O1CN01MkYkRh1L5ASelx4Nn_!!0-item_pic.jpg_500x500q90.jpg_.webp' 
            style={{height:300,width:300,margin:40}}/>
            <div>
                <Bottom/>
                <button className='button1'>加入购物车</button>
                <button className='button2'>立即购买</button>
            </div>
        </div>
    )
}
export default strawberry;